ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  
}

li {
  float: right;
  margin:0;
}

li a {
  display: block;
  color: white;
  text-align: center;
  margin: 0px 64px 0px 64px;
  text-decoration: none;
  padding:40px 0px ;
  font-size: large;
}

li a:hover {
  color: #ED490E;
}

.frontcontent{
  background-color: black;
  background-image: url(https://uploads-ssl.webflow.com/61d4b50a3b130a78a63644fe/61e57cced52b4c5f7554323e_Header%20Background.png);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: block;
  color: white;
  padding-top: 150px;
  text-align: center;
  position: relative;
  padding-bottom: 150px;
}

h1 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 5.25em;
  line-height: 1.05;
  font-weight: 400;
  letter-spacing: 0.03em;
}

.ringtext{
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mainpage{
  background-color: black;
  background-image: url(https://uploads-ssl.webflow.com/61d4b50a3b130a78a63644fe/61e57cced52b4c5f7554323e_Header%20Background.png);
}
